.processbar-container {
    padding: 0;
    max-width: 1200px;
    margin: 0 auto 100px;
}

.processbar-row {
    max-width: 992px;
    margin: 0 auto;
    padding: 1.2em 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
}

.processbar-left {
    -ms-flex: 1 0;
        flex: 1 0;
    text-align: right;
    min-width: 250px;
}

.processbar-right {
    -ms-flex: 3 0;
        flex: 3 0;
    position: relative;
}

.processbar-left,
.processbar-right {
    margin: 0 30px;
    width: 100%;
}

.processbar-left h2 {
    margin: 0;
    position: relative;
    top: -5px;
}

.processbar-svg {
    border-radius: 5px;
    width: 100%;
}

.processbar-counter {
    position: absolute;
    font-weight: normal;
    top: -45px;
    left: -30px;
    min-width: 60px;
    max-width: 60px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: 20px;
    letter-spacing: 1px;
    text-transform: uppercase;
    background-color: #FF92F6;
    color: #fff;
    padding: 5px 10px;
    opacity: 0;
    border-radius: 5px;
}

.processbar-counter:after {
    content: "";
    position: absolute;
    bottom: -9px;
    left: 50%;
    margin-left: -10px;
    border-width: 10px 10px 0;
    border-style: solid;
    border-color: #FF92F6 transparent;
    display: block;
    width: 0;
}

.processbar-counter span:nth-child(2) {
    font-size: 14px;
    font-weight: normal;
}

@media only screen and (max-width: 992px) {
    .processbar-left h2:nth-child(1) {
        margin-top: 10px;
        font-size: 40px;
    }
    .processbar-left h2:nth-child(2) {
        font-size: 30px;
    }
    .processbar-left,
    .processbar-right {
        margin: 0 10px;
    }
}

@media only screen and (max-width: 768px) {
    .processbar-left h2:nth-child(1) {
        margin-top: 0px;
        letter-spacing: 2px;
        font-size: 28px;
    }
    .processbar-container {
        padding: 45px 0;
        margin: 0 auto;
    }
    .processbar-row {
        -ms-flex-direction: column-reverse;
            flex-direction: column-reverse;
        padding: 30px 0 10px;
    }
    .processbar-left h2 {
        text-align: left;
    }
    .processbar-left {
        min-width: initial;
    }
    .processbar-left,
    .processbar-right {
        margin: 0;
        width: 100%;
    }
}


